.navbar {
  /* box-shadow: 0px 3px 5px grey; */
  /* box-shadow: 1px 2px 3px rgba(0,0,0,.5); */
  background-color: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(5px);
  font-family: Guttman Hatzvi;
}

.navbar .ml-auto {
  margin-right: auto!important;
  margin-left: 0!important;
}

.navbar a {
  /* color: #343a40; */
  color: rgba(233, 241, 239, 1);
  text-decoration: none!important;
  margin: 2px 9px 2px 9px;
}

section, footer {
  background-color: rgba(233, 241, 239, 1);
  /* backdrop-filter: blur(20px); */
}

section .jumbotron {
  background-color: transparent;
}

footer {
  padding:40px 0;
  /* background-color:#E9F1EF; */
  color:#4b4c4d;
}
  
footer ul {
  padding:0;
  list-style:none;
  text-align:center;
  font-size:18px;
  line-height:1.6;
  margin-bottom:0;
}

footer li {
  padding:0 10px;
}

footer ul a {
  color:inherit;
  text-decoration:none;
  opacity:0.8;
}

footer ul a:hover {
  opacity:1;
}

footer .social {
  text-align:center;
  padding-bottom:25px;
}

footer .social > a {
  font-size:24px;
  width:40px;
  height:40px;
  line-height:40px;
  display:inline-block;
  text-align:center;
  border-radius:50%;
  border:1px solid #ccc;
  margin:0 8px;
  color:inherit;
  opacity:0.75;
}

footer .social > a:hover {
  opacity:0.9;
}

footer .copyright {
  margin-top:15px;
  text-align:center;
  font-size:13px;
  color:#aaa;
  margin-bottom:0;
}